@import url(https://fonts.googleapis.com/css2?family=Inter:wght@700&display=swap);


 html, body{
     /*font-family: 'Roboto', sans-serif;*/
     font-family: 'Inter', sans-serif;
     
     font-weight: 700;
     color: #98B9F2;
     
 }
 
 a{
     color: #228BE6;
     
 }
 
 a:hover{
     color: #0f80e2;
 }
 
 a.no-underline{
     text-decoration: none;
 }
 
 .no-div-center{
     text-align: left;
     max-width: 600px;
     
 }
 div.center{
     
     display: flex;
     justify-content:center;
     
 }
 
 
 .card-wrapper{
     
     display: flex;
     
     
     padding-bottom: 40px;
     box-shadow: 0px 10px 20px black;
     border-radius: 10px;
     width: 75%;
     max-width: 1000px;
     margin-bottom: 50px;
     transition: 0.2s ease-in-out;
 }
 #card-projects{
   margin-top: 40px;
   
   
 }
 
 .card-wrapper-projects{
   width: 75%;
   
   max-width: 1200px;
   padding-bottom: 40px;
   box-shadow: 0px 10px 20px black;
   border-radius: 10px;
   
   
   margin-bottom: 50px;
   transition: 0.2s ease-in-out;
 }
 .card-wrapper-projects:hover{
   box-shadow: 0px 20px 40px black;
 }
 .card-wrapper:hover{
     
     box-shadow: 0px 20px 40px black;
 }
 
 .card{
     margin-left: 2rem;
     margin-right: 2rem;
     margin-bottom: 1rem;
     margin-top: 1rem;
     text-align: left;
     
     
 }
 
 body{
     padding-top: 100px;
     background-color: #141B41;
 }
 
 .title{
     color: #D34F73;
     line-height: 1em;
 }
 
 
